form {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

label {
  width: 320px;
  justify-content: space-between;
  font-size: 20px;
  font-weight: 500;
  display: flex;
}

button {
  width: 250px;
  background-color: #8ece89;
  border-radius: 20px;
  margin: 20px auto 0 20px;
  padding: 10px 30px;
  font-size: 20px;
  font-weight: 700;
  transition: box-shadow .3s, background-color .3s;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, .4), 4px 4px 4px 2px rgba(0, 0, 0, .2);
}

button:hover, button:focus {
  box-shadow: none;
  background-color: #a3f29e;
}

